.userTable {
  max-width: 1300px;
  margin: 0 auto;
  @media all and (max-width: 1500px) {
    padding: 2rem;
  }
}

.researchersTable {
  max-width: 1800px;
  margin: 0 auto;
  @media all and (max-width: 1500px) {
    padding: 2rem;
  }
}

.program {
  display: grid;
  grid-gap: 5px;
}

.results {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: 200px auto;
  grid-auto-rows: 100px auto;
}

.header-program-tests {
  grid-column-start: 1;
  grid-column-end: 3;
}

.users {
  display: grid;
  grid-template-columns: 1fr;
}

.user {
}

.emailBox {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-gap: 20px;
  align-items: baseline;
  justify-content: left;
  .button {
    text-align: center;
  }
}

.participantInformation {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 2rem;
  background: white;
  margin: 2rem 0rem;
  padding: 3rem;
  border: 1px solid lightgrey;
  width: fit-content;
}



